import React, { useState } from "react";
import { sideBarData } from "../../constants/sidebarStageData.js";
import "./SideBar.css";

const Sidebar = () => {
  const [searchText, setSearchText] = useState("");
  const [selectedTab, setSelectedTab] = useState(1);

  const handleDrag = (e, innerItem,id) => {
    const customData = {
      name: innerItem,
      type: id
    };
    switch (id.toString()) {
      case "1":
        // Handle the drop action for type 1
        e.dataTransfer.setData("flowChartDragEvent", JSON.stringify(customData));
        break;
      case "2":
        // Handle the drop action for type 2
        e.dataTransfer.setData("processOperationDragEvent", JSON.stringify(customData));
        break;
      case "3":
        // Handle the drop action for type 3
        e.dataTransfer.setData("processActionsDragEvent", JSON.stringify(customData));
        break;
      default:
        // Handle the drop action for other types or no type
        console.log("Dropped type:", id);
        break;
    }
  };


  return (
    <div className="sidebar-container">
      <div className="sidebar-menu">
        <div className="radio">
          <input
            id="radio-1"
            name="radio"
            type="radio"
            checked={selectedTab === 1}
            onChange={() => {
              setSelectedTab(1);
            }}
          />
          <label htmlFor="radio-1" className="radio-label">
            <span style={{ fontSize: "18px" }}>Catalogue</span>
          </label>
        </div>

        <div className="radio">
          <input
            id="radio-2"
            name="radio"
            type="radio"
            checked={selectedTab === 2}
            onChange={() => {
              setSelectedTab(2);
            }}
          />
          <label htmlFor="radio-2" className="radio-label">
            <span style={{ fontSize: "18px" }}>Template</span>
          </label>
        </div>
      </div>
      {selectedTab === 1 && (
        <>
          <div className="sidebar-search">
            <input
              onChange={(e) => setSearchText(e.target.value)}
              className="nosubmit"
              type="search"
              placeholder="Search..."
              value={searchText}
            />
          </div>
          <div className="sidebar-data-container">
            {sideBarData.map((item) => {
              const filteredChildren = item.children.filter((child) =>
                child.toLowerCase().includes(searchText.toLowerCase())
              );

              return (
                <div className="sidebar-process-data-title" key={item.title}>
                  <span>{item.title}</span>
                  {item.title === "Process Stages" &&
                    filteredChildren.map((innerItem) => (
                      <div
                        className="sidebar-process-data-children"
                        key={innerItem}
                      >
                        <li>
                          <span
                            onDragStart={(e) => handleDrag(e, innerItem,1)}
                            style={{ fontSize: "14px", cursor: "pointer" }}
                            draggable
                          >
                            {innerItem}
                          </span>
                        </li>
                      </div>
                    ))}
                  {item.title === "Process Operations" &&
                    filteredChildren.map((innerItem) => (
                      <div
                        className="sidebar-process-data-children"
                        key={innerItem}
                      >
                        <li>
                          <span
                            onDragStart={(e) => handleDrag(e, innerItem,2)}
                            style={{ fontSize: "14px", cursor: "pointer" }}
                            draggable
                          >
                            {innerItem}
                          </span>
                        </li>
                      </div>
                    ))}
                  {item.title === "Process Actions" &&
                    filteredChildren.map((innerItem) => (
                      <div
                        className="sidebar-process-data-children"
                        key={innerItem}
                      >
                        <li>
                          <span
                            onDragStart={(e) => handleDrag(e, innerItem,3)}
                            style={{ fontSize: "14px", cursor: "pointer" }}
                            draggable
                          >
                            {innerItem}
                          </span>
                        </li>
                      </div>
                    ))}

                  <div>
                    {filteredChildren.length === 0 && (
                      <span
                        style={{
                          fontSize: "14px",
                          fontStyle: "italic",
                          marginLeft: "2vw",
                        }}
                      >
                        No results found
                      </span>
                    )}
                  </div>
                </div>
              );
            })}
          </div>
        </>
      )}
    </div>
  );
};
export default Sidebar;
